.has-label div:nth-child(1) .card:before {
  content: "1"; }

.has-label div:nth-child(2) .card:before {
  content: "2"; }

.has-label div:nth-child(3) .card:before {
  content: "3"; }

.has-label div:nth-child(4) .card:before {
