.has-label .card:after {
  position: absolute;
  content: "";
  top: calc(-20 * 1.3px);
  left: -6px;
  border-top: 40px solid transparent;
  border-bottom: 40px solid transparent;
  border-right: 40px solid #da9052;
  transform: rotate(45deg); }

.has-label .card:before {
  position: absolute;
  top: 6px;
  left: 10px;
  padding: 0;
  text-align: center;
  font-size: 20px;
  line-height: 20px;
